import { PushNotificationsService } from './../../shared/services/push-notifications/push-notifications.service';
import { IonRouterOutlet } from '@ionic/angular';
import { ModalHandlerService } from './../../shared/services/modal-handler/modal-handler.service';
import { AlertService } from './../../shared/services/alert/alert.service';
import { LanguageService } from './../../shared/services/language/language.service';
import { environment } from './../../../environments/environment';
import { ErrorHandlerService } from './../../shared/interceptors/error-handler.service';
import { GlobalDataService } from 'src/app/shared/services/globalData/global-data.service';
import { Component, ElementRef, EventEmitter, OnInit, ViewChild } from '@angular/core';
import { LoginService } from './services/login.service';
import { Router, RouterOutlet } from '@angular/router';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';
import { StorageService } from 'src/app/shared/services/storage/storage.service';
import { StorageConstants } from 'src/app/shared/config/constants/storage.constants';
import { Toast } from '@capacitor/toast';
import { GamesEnum } from 'src/app/shared/models/game_play.model';
import { Dialog } from '@capacitor/dialog';
import { TranslateService } from '@ngx-translate/core';
import { SplashScreen } from '@capacitor/splash-screen';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {

  @ViewChild('passwordEyeRegister', { read: ElementRef }) passwordEye: ElementRef;
  eyeIcon: string = 'eye';
  passwordTypeInput = 'password';

  username: string;
  password: string;
  loading: boolean = false;
  rememberCredentials: boolean = false;
  
  checked:boolean = false;
  environment = environment;
  register: boolean;
  constructor(
    private router: Router,
    public gd: GlobalDataService,
    private errorHandler: ErrorHandlerService,
    public translate: TranslateService,
    private languageService: LanguageService,
    private alertService: AlertService,
  ) { }

  ngOnInit() {  }

  ionViewWillEnter(){
    this.hideSplash();
  }

  async hideSplash() {
    await SplashScreen.hide();
  }

  async changeLanguage() {
    const buttons = [
      { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
      { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
    ];
    let name1 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.SPANISH');
    let name2 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.ENGLISH');
    let name3 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.CATALAN');
    let name4 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.RUSSIAN');
    const inputs = [
      { name: name1, type: 'radio', label: name1, value: 'es', checked: (this.languageService.currentLanguage === 'es') ? true : false },
      { name: name2, type: 'radio', label: name2, value: 'en', checked: (this.languageService.currentLanguage === 'en') ? true : false },
      { name: name2, type: 'radio', label: name4, value: 'ru', checked: (this.languageService.currentLanguage === 'ru') ? true : false },
      { name: name2, type: 'radio', label: name3, value: 'ca', checked: (this.languageService.currentLanguage === 'ca') ? true : false },
    ];
    let result: any = await this.alertService.showBasicAlert(this.translate.instant('PAGES.LOGIN.TRANSLATIONS.CHANGE'),this.translate.instant('PAGES.LOGIN.TRANSLATIONS.SELECT'),buttons, inputs);
    if (result && result.data && result.role === 'accept') this.languageService.currentLanguage = result.data.values;
    this.languageService.changeLanguage(this.languageService.currentLanguage);
  }

  isValidEmail(email: string): boolean {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  async login() {
    try {
      this.loading = true;

      this.router.navigateByUrl(RouterConstants.CHECK_BEACON, { replaceUrl: true });
    } catch (error) {
      let error_message = await this.errorHandler.showMessage(error);
      await Toast.show({ text: error_message, duration: 'long', position: 'bottom' });
      await this.gd.removeSession();
      console.log("Error", error);
      this.loading = false;
    }
  }

  togglePasswordMode() {
    this.passwordTypeInput = this.passwordTypeInput === 'text' ? 'password' : 'text';
    this.eyeIcon = this.passwordTypeInput === 'text' ? 'eye-off' : 'eye';
    const nativeEl = this.passwordEye.nativeElement.querySelector('input');
    const inputSelection = nativeEl.selectionStart;
    nativeEl.focus();
    setTimeout(() => { nativeEl.setSelectionRange(inputSelection, inputSelection); }, 1);
  }
}
